/* General Styles */
body {
    margin: 0;
    font-family: 'Arial', sans-serif;
    background-color: #1a1a1a;
    color: white;
    align-items: center;
    padding: 10px;
  }
  
  .portfolio-container {
    display: flex;
    flex-direction: column;
  }
  
  .profile-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    max-width: 1200px;
    width: 100%;
  }
  
  .left-section {
    text-align: center;
  }
  
  .profile-pic {
    width: 200px;
    height: 200px;
    border: none;
    object-fit: cover;
    border-radius: 20% 40%;
    margin-top: 50px;
  }
  
  .right-section{
    padding: 80px;
  }
  
  .intro-section{
    padding: 20px;
    margin: 10px 0px;
    color: #f2f6f2;
  }
  
  h1 {
    font-size: 36px;
    color: #007BFF;
    align-items: center;
  }
  
  p {
    font-size: 18px;
    line-height: 1.6;
    color: #a89999;
  }
  
  .intro-text {
    margin: 10px 0;
    color: #a89999;
    padding: 20px;
    align-items: center;
  }
  
  /* Sidebar at Top Right */
  .top-sidebar {
    position: fixed;
    top: 10px;
    right: 10px;
    justify-content: left;
    width: 100%;
  }
  
  .sidebar {
    display: flex;
    flex-direction: row;
    background: transparent;
    padding: 10px;
    border-radius: 8px;
    justify-content: right;
    gap: 20px;
    font-size: 10px;
  
  }
  
  .sidebar a {
    text-decoration: none;
    color: #eff2f5;
    font-size: 16px;
    margin: 20px solid #89ead3;
    transition: transform 0.3s ;
  }
  
  .sidebar a:hover {
    color: #fff;
    transform: scale(1.1);
  }
  
  /* About Me Section */
  .about-section {
    text-align: center;
    padding: 20px;
    background: #1a1a1a;
    margin: 20px;
    border-radius: 8px;
    animation: fadeIn 1s ease-in-out;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
    transition: transform 0.3s ease, box-shadow 0.3s ease, background-color 0.3s ease;
  }
  
  .about-section:hover {
    transform: translateY(-10px) scale(1.02); /* Slight lift and scale on hover */
    box-shadow: 0 8px 12px rgba(172, 167, 167, 0.2);
    background-color: #1f1b1b; /* Slight change in background color on hover */
  }
  
  .about-details .det h3 {
    margin: 10px 0;
    font-size: 18px;
    color: #ccc;
    transition: color 0.3s ease, transform 0.3s ease; /* Smooth transition for color and scaling */
  }
  
  .about-details .det h3:hover {
    color: #a9afac; /* Change color on hover */
    transform: scale(1.05); /* Slight scale effect on hover */
  }
  
  .about-details .det p {
    font-size: 16px;
    color: #a89999;
    margin: 8px 0;
    transition: color 0.3s ease; /* Smooth transition for text color */
  }
  
  .about-details .det p:hover {
    color: #f0f0f0; /* Subtle change in text color on hover */
  }
  
  .about-details .det span {
    font-weight: bold;
    color: #d4cdcd;
    transition: color 0.3s ease; /* Smooth transition for color */
  }
  
  .about-details .det span:hover {
    color: #404342; /* Change color on hover */
  }
  
  
  /* Education Section */
  .title {
    font-size: 36px;
    color: #007BFF;
    text-align: center; /* Center the heading */
    margin-bottom: 20px;
    transition: transform 0.3s ease, color 0.3s ease; /* Add smooth transition */
  }
  
  .title:hover {
    color: #9aa29f; /* Change the color when hovering over the title */
    transform: scale(1.05); /* Slight scaling on hover */
  }
  
  .education-section {
    padding: 50px;
    background-color: #1a1a1a;
    animation: fadeIn 1s ease-in-out;
    display: flex;
    flex-direction: column;
    align-items: center; /* Center the content within the section */
  }
  
  .education-content {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 100%;
    max-width: 800px; /* Optionally, limit the width for better layout */
    animation: slideIn 1s ease-out;
    align-items: center; /* Center the cards within the content */
  }
  
  .education-card {
    background-color: #3e3c3c;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(162, 159, 159, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease, background-color 0.3s ease; /* Add transitions for hover effects */
    opacity: 0;
    transform: translateY(20px);
    animation: fadeUp 0.5s ease forwards;
    width: 100%;
    max-width: 600px;
  }
  
  .education-card:hover {
    transform: translateY(-10px) scale(1.02); /* Scale and lift the card slightly on hover */
    box-shadow: 0 8px 12px rgba(221, 216, 216, 0.6);
    background-color: #1f1b1b; /* Subtle change in background color on hover */
  }
  
  .education-card h3 {
    margin-bottom: 10px;
    color: #d4cdcd;
    transition: color 0.3s ease; /* Smooth transition for text color */
  }
  
  .education-card h3:hover {
    color: #9aaea6; /* Change the heading color on hover */
  }
  
  .education-card p {
    margin: 5px 0;
    color: #a89999;
  }
  
  .education-card span {
    font-weight: bold;
    color: #d4cdcd;
  }
  
  
  /* Animations */
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  @keyframes fadeUp {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes slideUp {
    from {
      transform: translateY(20px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
  
  @keyframes slideIn {
    from {
      opacity: 0;
      transform: translateX(-100px);
    }
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .profile-section {
      flex-direction: column;
      text-align: center;
    }
  
    .top-sidebar {
      top: 5px;
      right: 5px;
      position: fixed;
    }
  
    .sidebar{
      width: fit-content;
      position: fixed;
    }
  
    .experience-card {
      padding: 15px;
      text-align: center; /* Center align for better mobile view */
      margin: 15px auto;
      width: 90%; /* Adjust width for better readability */
    }
  
    /* Adjust the size of headings and text for small screens */
    .experience-card h2 {
      font-size: 1.5rem;
      margin-bottom: 10px;
    }
  
    .experience-card p {
      font-size: 1rem;
      margin-bottom: 10px;
    }
  
    /* Adjust button for mobile */
    .experience-card .website-link {
      width: 100%; /* Make the button full-width */
      padding: 12px 0;
      font-size: 1rem;
    }

    .buttons {
      flex-direction: column; /* Stack buttons vertically */
      gap: 15px; /* Adjust spacing between buttons */
  }

  .btn {
      width: 100%; /* Buttons take full width on mobile */
      max-width: 300px; /* Optional: set a max width for large screens */
  }

  
  
  }
  
  .social-links a {
    color: white;
    font-size: 24px;
    margin: 0 10px;
    transition: color 0.3s ease;
  }
  
  .social-links a:hover {
    color: #007BFF;
  }
  
  .buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px; /* Add space between the buttons */
    margin-top: 20px;
}

.btn {
    background-color: #007BFF;
    color: white;
    padding: 10px 20px;
    border-radius: 25px;
    text-decoration: none;
    transition: background-color 0.3s ease;
    display: inline-block;
    text-align: center;
    min-width: 80px; /* Ensure buttons have a minimum width */
}

.btn:hover {
    background-color: #0056b3;
}
  
  /* Technical Skills Section */
  .technical-skills {
    padding: 50px;
    text-align: center;
  }
  
  .technical-skills .head {
    margin-bottom: 40px;
    color: #007BFF;
  
  }
  
  .skills-list {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
  }
  
  .skill {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100px;
    text-align: center;
  }
  
  .skill-icon {
    font-size: 3rem;
    transition: transform 0.3s ease;
    height: 90%;
    width: 90%;
  
  }
  
  
  .skill-icon:hover {
    transform: scale(1.1); /* Slight zoom effect on hover */
    color: #007bff; /* Change color on hover */
  }
  
  .skill span {
    margin: 5px;
    font-size: 1rem;
    font-weight: 500;
  }
  
  .experience-section{
    text-align: center;
  }
  
  .experience-card {
    background-color: #1a1a1a;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(78, 75, 75, 0.1);
    text-align: left;
    transition: transform 0.5s ease, box-shadow 0.3s ease;
    margin: 10px;
    max-width: 100%; /* Ensure it doesn't overflow on mobile */
  }
  
  /* Ensure the content wraps properly and doesn't overflow */
  .experience-card h2,
  .experience-card p {
    margin-bottom: 10px;
    color: #d4cdcd;
  }
  
  /* Improve button appearance */
  .experience-card .website-link {
    background-color: #1a1a1a;
    color: white;
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 20px solid #fff;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.4);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    display: flex;
    align-items: flex-start;

  }
    
  
  .experience-card .website-link:hover {
    background-color: #82b5ec;
  }
  
  /* General Styles for Project Section */
  .project-section {
    padding: 20px;
    margin-top: 50px;
    font-size: 40px;
  }
  
  .heading {
    font-size: 2rem;
    margin-bottom: 20px;
    text-align: center;
    color: #007BFF;
    align-content: left;
  }
  
  .project-container {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .project-card {
    background-color: #1a1a1a;
    color: white;
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 20px solid #fff;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.4);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    display: flex;
    align-items: flex-start;
  }
  
  .project-card:hover {
    transform: translateY(-10px);
    box-shadow: 0px 8px 20px rgba(163, 161, 161, 0.6);
  }
  
  .icon-container {
    margin-right: 15px;
  }
  
  .project-icon {
    font-size: 2rem;
    color: #42a5f5;
  }
  
  /* Project Details */
  .project-details h3 {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }
  
  .project-details p {
    font-size: 1.1rem;
    color: #a9a3a3;
    line-height: 1.6;
  }
  
  /* Portfolio-specific Styles */
  .portfolio-link {
    margin-top: 10px;
    font-size: 1.1rem;
    color: #42a5f5;
    text-decoration: none;
  }
  
  .portfolio-link:hover {
    color: #1e88e5;
    text-decoration: underline;
  }
  
  /* HR Labs-specific Styles */
  .hrlabs-title,
  .portfolio-title {
    font-size: 1.8rem;
    margin-bottom: 10px;
  }
  
  
  
  
  